import { Descriptions } from "antd";
import { DetailBaseInfo } from "api/detailApi";

type BaseInfoProps = {
  info?: DetailBaseInfo;
};

export const BaseInfo = (props: BaseInfoProps) => {
  const { id, userName, website, qualifications, tradeLevel1, tradeLevel2, type, note } =
    props.info || {};
  return (
    <Descriptions title="XX-" bordered>
      <Descriptions.Item label="">{userName}</Descriptions.Item>
      <Descriptions.Item label="id">{id}</Descriptions.Item>
      <Descriptions.Item label="">{website}</Descriptions.Item>
      <Descriptions.Item label="">{qualifications}</Descriptions.Item>
      <Descriptions.Item label="">{tradeLevel1}</Descriptions.Item>
      <Descriptions.Item label="">{tradeLevel2}</Descriptions.Item>
      <Descriptions.Item label="">{type}</Descriptions.Item>
      <Descriptions.Item label="">{note}</Descriptions.Item>
    </Descriptions>
  );
};
